<template>
<div>
  <ul class="nav nav-tabs">
    <li class="nav-item" v-for="( item, idx ) in fav" v-bind:key="idx" :ref="'nav-' + idx">
      <a :class="'nav-link '  + ( selectedNavIdx==idx?'active':'' )" @click="renderFav( idx )">{{ item.label }}</a>
    </li>
  </ul>
  <div class="continer">
    <div class="row mt-3">
      <div class="d-grid gap-2">
        <!-- item start -->
        <div class="p-2 bg-light border" v-for="( item, index ) in viewFav.links" v-bind:key="index" @click="go(item.link)" style="cursor:pointer">
          <span class="fw-bold text-dark">{{item.title}}</span>
          &nbsp;
          <span class="fw-lighter text-dark" style="font-size:12px">{{item.description}}</span>
        </div>
        <!-- item end -->
      </div>
    </div>
  </div>
</div>
</template>
<script>
import fav from './favorite-data'

export default {
  name: 'app-home',
  props: {},
  data() {
    return {
      fav,
      selectedNavIdx : 0,
      viewFav : [],
      style : [ 'tile-blue', 'tile-green', 'tile-yellow', 'tile-pink', 'tile-purple', 'tile-lime', 'tile-teal', 'tile-turquoise', 'tile-greensea', 'tile-emerald', 'tile-nephritis', 'tile-amethyst', 'tile-midnightblue', 'tile-orange', 'tile-carrot', 'tile-pumpkin', 'tile-alizarin', 'tile-clouds', 'tile-silver', 'tile-concrete', 'tile-asbestos', ]
    }
  },
  mounted() {
    this.renderFav( this.selectedNavIdx )
  },
  methods : {
    renderFav( idx ) {
      this.selectedNavIdx = idx
      this.viewFav = fav[ idx ]
    },
    tileColor( idx ) {
        return [ 'thumbnail', 'tile', 'tile-wide', this.style[ idx % this.style.length ] ]
    },
    go( url ) {
      window.location.href = url
    },
  },
}
</script>

